@import url(https://fonts.googleapis.com/css2?family=Raleway&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.header {
    display: flex;
    align-items: center;
    height: 2rem;
    background: rgba(19,19,19,255)
}

.header div {
    display: flex;
    align-items: center;
    margin: 0 auto;
}

.header span, a{
    font-weight: bold;
    font-family: 'Raleway', sans-serif;
}

.header a ,span:hover{
    cursor: pointer;
}

.header * {
    margin: 0.5rem;
    font-size: 0.8rem;
    color: rgba(123,123,123,255)
}





.container {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding-top: 60px;
    padding-bottom: 100px;
    outline: none;
    
  }

.sign-frame {
    height: 300px;
    width: 250px;
    background:
      linear-gradient(
      rgba(35,43,85,0.75),
      rgba(35,43,85,0.95));
      background-size: cover;
  margin-left: auto;
  margin-right: auto;
  border-top: solid 1px rgba(255,255,255,.5);
  border-radius: 5px;
  box-shadow: 0px 2px 7px rgba(0,0,0,0.2);
  overflow: hidden;
  transition: all .5s ease;
}

.sign-nav {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin: 20px;
}

.login-btn {
  cursor: pointer;
  color: white;
  border: 3px solid white;
  border-radius: 10px;
  background: transparent;
  padding: 3px 10px 3px 10px;
}

.login-btn:hover {
  background: rgba(57, 63, 90, 0.75);
}

.login-btn:active {
  background: rgba(12, 26, 87, 0.75);
}

.close-btn {
  cursor: pointer
}

.btn-signin {
  color: #efefef;
  cursor: pointer;
}
.sign-input {
  background: transparent;
  border: none;
  outline: none;
  border-bottom: solid 1px #efefef;
  margin: 4px;
  color: #efefef;
}

.form-signup {
    text-align: center;
    font-size: small;
}

.form-signup input{
    width: 50%;
}



.main-div {
    display: flex ;
    align-items: center;
    justify-content: center;
    background-color: rgba(12,66,112,255);
    height: 4rem;
    width: 100%;
}   

.logo{
    display: flex;
    align-items: center;
    width: 30%;
    padding-left: 40px;
}

.logo img{
    width: 4rem;
}

.logo span {
    color: white;
    font-family: 'Roboto', sans-serif;
    font-size: x-large;
}

.menu{
    width: 50%;
}

.menu span{
    margin: 15px;
    font-weight: bold;
    font-family: 'Raleway', sans-serif;
    font-size: 0.8rem;
    color: white
}
.hero-img {
    position: relative;
    width: 100%;
    height: 35rem;
    background-image: linear-gradient(rgba(138, 208, 255, 0), rgba(89, 169, 255, 0.7)), url(/static/media/resort.61f8f204.jpg);
    background-size: cover;
    background-repeat: no-repeat;

}
.hero-img img{
    /* width: 100%;
    height: 600px; */
}

.hero-text {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-shadow: 2px 0 0 rgba(123,123,123,255), -2px 0 0 rgba(123,123,123,255), 0 2px 0 rgba(123,123,123,255), 0 -2px 0 rgba(123,123,123,255), 1px 1px rgba(123,123,123,255), -1px -1px 0 rgba(123,123,123,255), 1px -1px 0 rgba(123,123,123,255), -1px 1px 0 rgba(123,123,123,255);
    color: white;   
    font-size: xx-large;
    font-weight: bold;
    font-family: 'Raleway', sans-serif;
    text-align: center;
}
.catalog-container {
    display: flex;
    flex-wrap: wrap;     
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    align-content: center;
    margin-top:20px;
    margin-bottom: 40px;
}

.catalog-header {
    text-align: center;
    font-weight: bold;
    font-family: 'Raleway', sans-serif;
    color: rgb(75, 75, 75)  ;
}

.catalog-container > div {
    position: relative;
    flex-basis: 30%;    
    height: 300px;
    margin-bottom: 120px;
}

.vac-box{
    /* background-image: linear-gradient(rgba(138, 208, 255, 0), rgba(89, 169, 255, 0.7)); */
    background-repeat: no-repeat;
    background-size: cover;
    height: 100%;
}

.h1 {
    text-align: center;
}

.catalog-hr {
    width: 5%;
    background-color: rgb(96, 178, 255);
    border: 0 none;
    height: 3px; 
}

.vacations-container{
    width: 100%;
}

.follow-btn {
    font-size: 1.5rem;
    background-color: transparent;
    border: none;
